import React, { Component } from "react";
import { withRouter } from "react-router";
import Courses from "@components/Courses";
import NewCourse from "@components/NewCourse";
import DocumentSettings from "@utils/seo";

function CoursesScreen(props: { courses: Array<any> }) {
    const seo: any = {
        title: "Eğitimlerim | Can Gökçeaslan",
        description: "En son video eğitimlerime bu sayfadan ulaşabilirsiniz",
        keywords: "Udemy kurslarım, Udemy, Node.js, Video Kurs, Video Kurslarım, Udemy, Udemy JavaScript, Udemy Ücretsiz Kurslar, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
        author: "Can Gökçeaslan",
        locale: "tr",
        image: "https://www.cangokceaslan.com/favicon.ico",
        siteName: "https://www.cangokceaslan.com"
    }
    return (
        <div>
            <DocumentSettings docData={seo} />
            <Courses />
            <NewCourse />
        </div>
    )
}

export default withRouter(CoursesScreen);